<template>
    <div class="great">
        <van-nav-bar title="彩生活上市五周年庆" left-text="" left-arrow @click-left="onClickLeft" />
        <div class="content1">
            <!-- <p>第一页</p> -->
            <!-- 动画 -->
            <div>
                <div class="ballon"></div>
            </div>

        </div>
        <div class="content2">
            <!-- <p>第二页</p> -->
            <video  controls>
                <source src="../../../../static/video/movie.mp4" type="video/mp4">
            </video>
        </div>
        <div class="content3">
            <!-- <p>第三页</p> -->
            <div class="music">
                <van-row>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" /> 逆战</label>
                    </van-col>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" />我们不一样</label>
                    </van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" /> 我的未来不是梦</label>
                    </van-col>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" />光阴的故事</label>
                    </van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" /> 向天再借五百年</label>
                    </van-col>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" />越过山丘</label>
                    </van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" /> 最美的太阳</label>
                    </van-col>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" />倔强</label>
                    </van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" /> 像我这样的人</label>
                    </van-col>
                    <van-col span="12">
                        <label><input name="Fruit" type="checkbox" value="" />朋友</label>
                    </van-col>
                </van-row>

            </div>
            <van-button type="default" class="my-btn">提交</van-button>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'

import { Row, Col } from 'vant'
import { Button } from 'vant'
import { Swipe, SwipeItem } from 'vant'

Vue.use(Row)
    .use(Col)
    .use(Button)
    .use(Swipe)
    .use(SwipeItem)
export default {
    name: 'great',
    data() {
        return {}
    },
    methods: {
        onClickLeft() {
            this.$router.go(-1)
        },
    },
}
</script>
<style lang="scss" scoped>
.van-nav-bar .van-icon {
    color: #000;
}
.van-nav-bar__title {
    font-size: 0.9rem;
}
.great {
    width: 100%;
    // height: 111.25rem;
    .content1 {
        background: url('../../../../static/img/great1-2.jpg') no-repeat center
            top;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        > div {
            padding-bottom: 1.87rem;
        }
        p {
            text-align: center;
        }
    }
    .content2 {
        background: url('../../../../static/img/great2.jpg') no-repeat center
            top;
    }
    .content3 {
        background: url('../../../../static/img/great3.jpg') no-repeat center
            top;
    }
    .content1,
    .content2,
    .content3 {
        width: 100%;
        height: 32.82rem;
        background-size: 100% 100%;
        color: white;
        p {
            margin-top: 0;
        }
    }
}
//下面是动画
@keyframes scaleDraw {
    /*定义关键帧、scaleDrew是需要绑定到选择器的关键帧名称*/
    0% {
        transform: scale(1); /*开始为原始大小*/
    }
    25% {
        transform: scale(1.1); /*放大1.1倍*/
    }
    50% {
        transform: scale(1);
    }
    75% {
        transform: scale(1.1);
    }
}
.ballon {
    border-radius: 50%;
    width: 60px;
    height: 60px;
    background: url('../../../../static/img/Downarrow@3x.png');
    background-size: 60px 60px;
    -webkit-animation-name: scaleDraw; /*关键帧名称*/
    -webkit-animation-timing-function: ease-in-out; /*动画的速度曲线*/
    -webkit-animation-iteration-count: infinite; /*动画播放的次数*/
    -webkit-animation-duration: 5s; /*动画所花费的时间*/
}

.music {
    padding-top: 10rem;
}
.van-row {
    text-align: left;
    padding-left: 1.8rem;
    margin-top: 1.25rem;
}
.my-btn {
    width: 12rem;
    margin-top: 2.37rem;
}

.content2 {
    video {
        margin-top: 7.4rem;
        width: 17rem;
        height: 13rem;
    }
}
</style>



